import React, { useState } from 'react';
import Axios from 'axios';
import MovieRow from './MovieRow';

const App = () => {
  const [query, setQuery] = useState('');
  const [movieRows, setMovieRows] = useState([]);
  const url = `https://api.themoviedb.org/3/search/movie?api_key=1b5adf76a72a13bad99b8fc0c68cb085&query=${query}`;

  const getData = async () => {
    await Axios.get(url)
    // console.log(response)
    .then(response => {
      setQuery('');
    setMovieRows(response.data.results);
    })
    .catch(error => {
      alert('Please enter a correct movie name');
    });
  };


  const onSubmit = (e) => {
    e.preventDefault();
    if (query === ''){
      alert('Please enter a movie name');
    } else {
      getData();
    }
  };

  const searchChangeHandler = (e) => {
    setQuery(e.target.value);
  // console.log(e.target.value);
  }

  return (
    <>
    <div className="container-fluid" style={{backgroundColor: 'black', padding: 30}}>
     <div style={{
        backgroundColor: "#000",
        color: "#fff",
        fontSize: 24,
        textAlign: 'center',
        paddingLeft: 100,
        display: 'flex'
     }}>
   
<img alt="logo" width="80" src="https://www.themoviedb.org/assets/1/v4/logos/312x276-primary-green-74212f6247252a023be0f02a5a45794925c3689117da9d20ffe47742a665c518.png" />
<h3 style={{marginTop: '12px', marginLeft: '20px'}}>MovieDB Search Application</h3>
  </div>

<form onSubmit={onSubmit}>
<div className='container' style={{borderRadius: 30, marginTop: 15}}>
<input style={{
  fontSize: 16,
  width: "70%",
  paddingTop: 2,
  paddingBottom: 8,
  paddingLeft: 16,
  marginTop: 15
}} 
placeholder="Search movies here..." 
onChange={searchChangeHandler}
autoComplete='off' 
value={query}/>
&nbsp;&nbsp;&nbsp;
<button style={{
  fontSize: 18,
  width: "20%"
}}
className='btn btn-success' onClick={getData}>Search</button>
</div>
</form> 
</div>

<div className='container'>
<MovieRow movieRows={movieRows} /> 
</div>
</>
)}

export default App;