import React from 'react'
import ReadMoreReact from 'read-more-react';

const MovieRow = (props) => {

  return (
    <>
    <div className='row' style={{fontFamily: 'calibri', marginBottom:'45px'}}>
      {
        props.movieRows.map(movieMapRow => 
     <div key={movieMapRow.id} className='col-xs-6 col-sm-6 col-md-4 col-lg-3 col-xl-3'  style={{backgroundColor: '#f8f8ff', fontFamily: 'nunito sans'}}>
      <div className='card' style={{marginBottom: 35, marginTop: 30, background: 'linear-gradient(90deg, #6190E8, #A7BFE8)', height: '100%', color: 'black'}}>
      <div className="card-header">
      <img src={`https://image.tmdb.org/t/p/w185/${movieMapRow.poster_path}`} className='img-fluid mx-auto w-100' alt={movieMapRow.title} />
          </div>
              <div className="card-body">
              <h6><b>Title: </b>{movieMapRow.title}</h6>
          <h6><b>Release date: </b>{movieMapRow.release_date}</h6>
          <h6><b>Overview: </b>
          <div style={{cursor:'pointer'}}>
     <ReadMoreReact text={movieMapRow.overview} /> </div>
              </h6>
            </div>
          </div>
        </div>
          )}
    </div>
    </>
  )
}


export default MovieRow;